.top-left{-webkit-animation:tl 7.5s ease infinite;animation:tl 7.5s ease infinite}
.top-right{-webkit-animation:tr 7.5s ease infinite;animation:tr 7.5s ease infinite}
.bottom-left{-webkit-animation:bl 7.5s ease infinite;animation:bl 7.5s ease infinite}
.bottom-right{-webkit-animation:br 7.5s ease infinite;animation:br 7.5s ease infinite}

.loading-container div:before,
.loading-container div:after
	{display:block;position:absolute;content:""}

.loading-container div:before{width:50px;height:50px}

.top-left:before{
	background:#fbbc05;
	top:0;
	left:0;
	border-radius:100% 0 0 0
	}

.top-right:before{
	background:#ea4335;
	top:0;
	right:0;
	border-radius:0 100% 0 0
	}

.bottom-left:before{
	background:#34a853;
	bottom:0;
	left:0;
	border-radius:0 0 0 100%
	}

.bottom-right:before{
	background:#4285f4;
	bottom:0;
	right:0;
	border-radius:0 0 100% 0
	}

