@-webkit-keyframes tl{
	0%,96%,100%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}
	12%{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}
	24%{-webkit-transform:rotate(-90deg);transform:rotate(-90deg)}
	36%{-webkit-transform:rotate(-135deg);transform:rotate(-135deg)}
	48%{left:50px;-webkit-transform:rotate(-180deg);transform:rotate(-180deg)}
	60%{left:125px;opacity:1;-webkit-transform:rotate(-135deg);transform:rotate(-135deg)}
	61%,95%{opacity:0}
	}

@keyframes tl{
	0%,96%,100%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}
	12%{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}
	24%{-webkit-transform:rotate(-90deg);transform:rotate(-90deg)}
	36%{-webkit-transform:rotate(-135deg);transform:rotate(-135deg)}
	48%{left:50px;-webkit-transform:rotate(-180deg);transform:rotate(-180deg)}
	60%{left:125px;opacity:1;-webkit-transform:rotate(-135deg);transform:rotate(-135deg)}
	61%,95%{opacity:0}
	}

@-webkit-keyframes tr{
	0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}
	12%{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}
	24%{-webkit-transform:rotate(-90deg);transform:rotate(-90deg)}
	36%{-webkit-transform:rotate(-135deg);transform:rotate(-135deg)}
	48%{-webkit-transform:rotate(-180deg);transform:rotate(-180deg)}
